.root {
    background-color: #31315c;
    width: 870px;
    margin: 100px auto;
    padding: 23px 35px 50px;
    min-height: 500px;
    border-radius: 16px;
    box-shadow: 0 5px 53px 2px rgba(34, 60, 80, 0.4);
    display: flex;
    flex-direction: column;
}

.root__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
}

.root__hr {
    height: 1px;
    width: 100%;
    background-color: #43446a;
    margin-bottom: 20px;
}
.filterSearch {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}



