.root {
  width: 100%;
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr;
}

.editMode {
  grid-template-columns: 1fr 1fr;
}
.editMode textarea {
  grid-area: 2/1/3/4;
}

.root input,
.root textarea {
  padding: 12px 20px;
  font-size: 16px;
  background-color: #272851;
  max-width: 250px;
  font-family: Montserrat, serif;
  color: #ffffff;
  border-radius: 15px;
  border: 1px solid transparent;
}
.root textarea {
  max-width: 100%;
  height: 200px;
  resize: none;
}

.root input:focus,
.root textarea:focus {
  outline-offset: -3px;
}

form .error {
  border: 1px solid #da2828;
}

.error::placeholder {
  color: #da2828;
}

.btn {
  margin-top: 10px;
  background-color: #3e79b0;
  color: #ffffff;
  padding: 12px 40px;
  border-radius: 15px;
  font-family: Montserrat, serif;
  font-weight: 600;
  max-width: 190px;
}

.btns {
  grid-area: 1/3/1/4;
  display: flex;
  gap: 10px;
}

.confirm {
  color: #7fd0b0;
}

.confirm:hover {
  color: #19db8e;
}

.cancel {
  color: #de6276;
}

.cancel:hover {
  color: #db3b50;
}
