.root {
  display: flex;
  align-items: flex-start;
  gap: 15px;
  position: relative;
  background-color: #43446a;
  padding: 16px 18px 22px;
  border-radius: 15px;
}

.root > div {
  width: 100%;
}

.root h3 {
  margin-bottom: 6px;
}

.root p {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.4;
  white-space: pre-line;
}


.root__customCheckbox {
  cursor: pointer;
  color: white;
}

.root__customCheckbox svg {
  width: 25px;
  height: 25px;
}

.root__header {
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.root__header h3 {
  flex-grow: 1;
}

.root__edit {
  flex-shrink: 0;
  color: #92beac;
}

.root__edit:hover {
  color: #19db8e;
}

.root__delete {
  color: #de6276;
  flex-shrink: 0;
}

.root__delete:hover {
  color: #db3b50;
}

.root__zip {
  color: #afaa52;
}
.root__zip:hover {
  color: #dbea0f;
}
