.root {
  max-height: 0;
  transition: max-height 0.3s, margin-bottom 0.2s;
  overflow: hidden;
}

.root__form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.root__btn {
  margin-top: 10px;
  background-color: #3e79b0;
  color: #ffffff;
  padding: 12px 40px;
  border-radius: 15px;
  font-family: Montserrat, serif;
  font-weight: 600;
  max-width: 190px;
}

.root input,
.root textarea {
  padding: 12px 20px;
  font-size: 16px;
  background-color: #272851;
  max-width: 250px;
  font-family: Montserrat, serif;
  color: #ffffff;
  border-radius: 15px;
  border: 1px solid transparent;
}

.root input:focus,
.root textarea:focus {
  outline-offset: -3px;
}

.root textarea {
  max-width: 100%;
  height: 200px;
  resize: none;
}

.show {
  max-height: 315px;
  margin-bottom: 20px;
}

.root__hr {
  height: 1px;
  width: 100%;
  background-color: #43446a;
  margin: 20px 0;
}

.root__form .error {
  border: 1px solid #da2828;
}

.root__form .error::placeholder {
  color: #da2828;
}
