.root {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.root span {
    font-size: 53px;
}
.root p {
    display: flex;
    align-items: center;
    gap: 6px;
}
.root p button {
    height: 25px;
    width: 25px;
    color: inherit;
}